/** ***************
 createCategory   input,     category
**************** */
/*
 
1.     data-video.js...
2.   dataFrontVideo
3.   dataFrontVideo   inputArrays
      
4.   dataFrontVideo   allVideo       
5.   dataFrontVideo   category    
5.   dataFrontVideo   learned     
6.     
  6.1              
  6.2              
  6.3            
  6.4     isLearned === true,      dataFrontVideo.learned
7.   dataFrontVideo
*/

export default function createCategory(data) {
  // 4.   result
  const result = {};
  result.result = {};
  result.learned = [];

  // 6.    data
  data.forEach((elem) => {
    // 6.1              
    if (elem.category && !(elem.category in result.result)) {
      result.result[elem.category] = {};
    }

    // 6.2              
    if (elem.theme && !(elem.theme in result.result[elem.category])) {
      result.result[elem.category][elem.theme] = [];
    }

    // 6.3            
    if (elem.category && elem.theme) {
      result.result[elem.category][elem.theme].push(elem);
    }

    // 6.4     isLearned === true,      learned
    if (elem.isLearned === true) {
      result.learned.push(elem);
    }
  });

  // 9.   result
  return result;
}
