/** ***************
 sortVideo    ,   
**************** */
export default function sortVideo(video) {
  video.sort((a, b) => {
    if (a.year === b.year) {
      //       
      if (a.author === b.author) {
        //       
        return a.title.localeCompare(b.title, 'en');
      }
      //    
      return a.author.localeCompare(b.title, 'en');
    }
    //    
    return a.year - b.year;
  });
  return video;
}
