/** ***************
 createList    li      aside 
**************** */
/*
 
1.  createList      
2.      
3.   parentDiv   li 
4.   elementLi  
5.   video    
  5.1  time  video      
6.       
7.   elementLi   parentDiv   li 
*/

import sortVideo from './sort-video';

// 1.  createList      
export default function createList(data) {
  // 2.    
  data = sortVideo(data);

  // 3.   parentDiv   li 
  const parentDiv = [];
  for (let i = 0; i < data.length; i++) {
    // 4.   elementLi  
    const elementLi = document.createElement('li');
    elementLi.addEventListener('mouseover', addColor);
    elementLi.addEventListener('mouseout', removeColor);

    // 5.   video    
    const video = {};

    // 5.1  time  video      
    if (data[i].time_h_m) {
      video.time = ` ${data[i].time_h_m[0] ? `${data[i].time_h_m[0]}` : ''} ${data[i].time_h_m[1] ? `${data[i].time_h_m[1]}` : ''}`;
    } else {
      video.time = ` ${data[i].time}`;
    }

    // 6.       
    elementLi.innerHTML = `
    <strong>[${data[i].year}] ${data[i].title}</strong>
    <div>${data[i].author} ${data[i].name}</div>
    <div>${data[i].numberLessons} ${video.time}  ${data[i].size}</div>
    <div> ${data[i].isLearned}</div>`;

    // 7.   elementLi   parentDiv   li 
    parentDiv.push(elementLi);

    function addColor() {
      elementLi.classList.add('over');
    }

    function removeColor() {
      elementLi.classList.remove('over');
    }
  }

  return parentDiv;
}
