/** ***************
 sortBooks    ,   
**************** */
export default function sortBooks(books) {
  books.sort((a, b) => {
    if (a.year === b.year) {
      //       
      if (a.author === b.author) {
        //       
        return a.title.localeCompare(b.title, 'en');
      }
      //    
      return a.author.localeCompare(b.author, 'en');
    }
    //    
    return a.year - b.year;
  });
  return books;
}

