/** **************
   front-code.js
 showFrontCode   front-code
****************** */
/*
 
1.   asideThemes  
2.   asideDiv   aside 
3.      asideThemes
  3.1   elementAside
  3.2   elementAside   asideDiv
  3.3     elementAside
    3.3.1   class="active"
4.   aside
5.     aside
6.     click   aside 
*/

import './scss/table.scss';
import './scss/code-js.scss';
import './element-front-code';
import pageLink from './pages/pages';

// 1.   asideThemes  
const asideThemes = ['HTML', 'CSS', 'Javascript', 'Typescript', 'C#', 'Markdown', 'Pug', 'SASS', 'PHP'];

// 2.   asideDiv   aside 
const asideDiv = [];

// 3.      asideThemes
for (const theme of asideThemes) {
  // 3.1   elementAside
  const elementAside = document.createElement('a');
  elementAside.href = '#';
  elementAside.innerHTML = theme;
  elementAside.id = createId(theme);

  function createId(str) {
    //  ,    
    return `code-${str.replace(/[^a-z0-9]/gi, '').toLowerCase()}`;
  }

  // 3.2   elementAside   asideDiv
  asideDiv.push(elementAside);

  // 3.3     elementAside
  elementAside.addEventListener('click', (e) => {
    e.preventDefault();

    // 3.3.1   class="active"
    classActive(asideDiv, elementAside);
  });
}

export default function showFrontCode() {
  const aside = document.querySelector('.aside');

  // 4.   aside
  aside.innerHTML = '';
  aside.append(...asideDiv);

  // 5.     aside
  pageLink();

  // 6.     click   aside 
  const eventClick = new Event('click');
  asideDiv[0].dispatchEvent(eventClick);
}

//  class="active"       
function classActive(elementOl, elementLi) {
  elementOl.forEach((li) => {
    li.classList.remove('active');
  });
  elementLi.classList.add('active');
}
