/** **************
   front-edu.js
 showFrontEdu   front-edu
****************** */
/*
 
1.   asideThemes  
2.   asideDiv   aside 
3.      asideThemes
  3.1   elementAside
  3.2   elementAside   asideDiv
  3.3     elementAside
    3.3.1   class="active"
4.   aside
5.     aside
6.     click   aside 
*/

import './element-front-edu';
import pageLink from './pages/pages';
import contentOrder from '../../../js/order';

// 1.   asideThemes  
const asideThemes = ['DSR 2023 .Net', 'RSS 2023 Stage1', 'Courses', 'Courses learned', 'Learning', 'RS School', 'RSS 2022 Stage0', 'RSS 2022 Stage1', 'RSS 2022 Stage3 Angular', 'RSS 2023 Stage0', 'Quiz', 'JS Examples'];

// 2.   asideDiv   aside 
export const asideDiv = [];

// 3.      asideThemes
for (const theme of asideThemes) {
  // 3.1   elementAside
  const elementAside = document.createElement('a');
  elementAside.href = '#';
  elementAside.innerHTML = theme;
  elementAside.id = createId(theme);

  function createId(str) {
    //  ,    
    return `edu-${str.replace(/[^a-z0-9]/gi, '').toLowerCase()}`;
  }

  // 3.2   elementAside   asideDiv
  asideDiv.push(elementAside);

  // 3.3     elementAside
  elementAside.addEventListener('click', (e) => {
    e.preventDefault();

    // 3.3.1   class="active"
    classActive(asideDiv, elementAside);
  });
}

export default function showFrontEdu() {
  const aside = document.querySelector('.aside');

  // 4.   aside
  aside.innerHTML = '';
  aside.append(...asideDiv);

  // 5.     aside
  pageLink();
  contentOrder();

  // 7.  class="activ"    aside 
  classActive(asideDiv, null);
}

//  class="active"       
function classActive(elementOl, elementLi = null) {
  elementOl.forEach((li) => {
    li.classList.remove('active');
  });
  if (elementLi) {
    elementLi.classList.add('active');
  }
}
