/** ***************
 createCategory   input,     category
**************** */
/*
 
1.   result
2.    
  2.1    menu-nav  
  2.2   youtube            
  2.3        category
3.   result
*/

export default function createCategory(data) {
  // 1.   result
  const result = [];

  // 2.     data
  data.forEach((elem) => {
    // 2.2   youtube            
    if (elem.theme && !(elem.theme in result)) {
      result[elem.theme] = [];
    }

    // 6.2        youtube 
    if (elem.theme) {
      result[elem.theme].push(elem);
    }
  });

  // 3.   result
  return result;
}
